import { Flex, Icon, keyframes, VStack } from '@chakra-ui/react'
import { FC } from 'react'
import { ImSpinner8 } from 'react-icons/im'

const animationKeyframes = keyframes`
  0% { transform: rotate(0); }
  100% { transform: rotate(360deg); }
`

const animation = `${animationKeyframes} 1s infinite linear`

export const Loader: FC = () => {
  return <Icon as={ImSpinner8} animation={animation} w="1.125rem" h="1.125rem" color="red.500" />
}

export const FullPageLoader = ({ dataTestId = 'FullPageLoader' }: { dataTestId?: string }) => {
  return (
    <VStack height="100vh" w="100vw" data-testid={dataTestId}>
      <Flex justifyContent="center" alignItems="center" height="100%">
        <Loader />
      </Flex>
    </VStack>
  )
}
